import React from 'react'
import { Link } from "react-router-dom"
// import parser from 'html-react-parser';

import DropdownMenu, { DropdownItem } from '../components/dropdown'
import { ReactComponent as Warn } from '../icons/warn.svg'
import { ReactComponent as DownArrow } from '../icons/downArrow.svg'
const UserDisplay = (props) => {

    // const banner = true;
    const banner = false;
    return (
        <>
            <div className="jumbotron-x user-jumbotron p-0 pb-2 pt-0 mt-5 mb-3 custom-card2 noselect">
                <div className="row">

                    {banner &&
                        (<div className="col-lg-12">
                            <img src="https://i.picsum.photos/id/309/1200/400.jpg?hmac=3a2r0DLtdbclTsmxoHwN54krvcmb3yrsLrUjmLqmNqk" alt="" className="w-100 banner" />
                        </div>)}

                    <div className="col-md-1 "></div>
                    <div className={`col-md-11 ${(!banner) ? "mt-3" : ""}`}>
                        <div className="row">
                            <div className="col-md-3 text-center text-sm-center text-md-left">
                                <img className={`d-inline-block avatar_image mt-0 mb-0 ${banner ? "with-banner" : ""}`}
                                    src={props.dp} alt="user" />
                            </div>
                            <div className="col-md-8 text-center text-sm-center text-md-left">
                                <div className="w-100 pl-sm-2 pr-sm-2 p-0">
                                    <h1 className="display-5-x name mt-2">{props.name}</h1>
                                    <h5 className="display-5-x username">@{props.username}</h5>
                                    <div className="lead user-bio pb-1">{props.bio}</div>
                                    <div className="f-count d-inline-block">0 Followers </div>&nbsp;&nbsp;
                                    <div className="f-count d-inline-block">0 Following </div>

                                    <div className="w-100 text-centerx pt-3">
                                        {(() => {
                                            if (props.mode === "self") {
                                                return (
                                                    <>
                                                        <Link to="/settings" className="btn btn-outline-secondary btn-sm" type="button">Edit profile</Link>
                                                    </>
                                                )
                                            } else
                                                return (
                                                    <>
                                                        <button className="btn btn-outline-primary btn-sm">
                                                            {/* <Add style={{
                                                                width: 18,
                                                                height: 18,
                                                                color: "#fff"
                                                            }} /> */}
                                                        Follow
                                                        </button>

                                                        {/* <button className="btn btn-outline-light btn-sm ml-2"><Add style={{ width: "0.875rem", height: "0.875rem" }}></Add> More</button> */}
                                                        {/* <div className="btn btn-outline-light btn-sm ml-2"></div> */}


                                                        <DropdownMenu
                                                            containerClassName="cursor-pointer noselect text-left"
                                                            autoClose
                                                            autoCloseTouch
                                                            containerStyle={
                                                                { cursor: "pointer" }
                                                            }
                                                            title={<div className="d-inline btnx btn-smx flex flex-row items-center ml-3">
                                                                <DownArrow className="text"
                                                                 style={{ width: "0.875rem", height: "0.875rem", top: "0.125rem"}} 
                                                                 /> More
                                                            </div>}
                                                        >
                                                            <DropdownItem 
                                                            icon={<Warn 
                                                            className="position-relative" 
                                                            style={{ width: "1rem", height: "1rem", top: "-0.125rem", left: "4px" }} />} 
                                                            type="link" to={`/report`}>&nbsp;&nbsp;Report</DropdownItem>

                                                        </DropdownMenu>
                                                    </>
                                                )
                                        })()}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-12">

                    </div>
                </div>
            </div>
        </>);
}

export default UserDisplay;